@import 'mixin.scss';

body, html {
  height: 100%;
}

.main-interface {
  background-color: LightSteelBlue;
  width: 100%;
  min-width: 400px; /* –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –±–ª–æ–∫–∞ */
  padding: 2rem;
  display: grid;
  place-items: center;
  margin-top: 80px;
  margin-bottom: 80px;
  height: auto;
  box-sizing: border-box; /* –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –æ—Ç—Å—Ç—É–ø—ã –Ω–µ –≤–ª–∏—è—é—Ç –Ω–∞ —à–∏—Ä–∏–Ω—É */
}

/* –ü—Ä–∏ —à–∏—Ä–∏–Ω–µ —ç–∫—Ä–∞–Ω–∞ –º–µ–Ω—å—à–µ 400px */
@media (max-width: 400px) {
  .main-interface {
    width: 100%; /* –®–∏—Ä–∏–Ω–∞ –Ω–∞ 100% */
    min-width: 400px; /* –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–µ–Ω–∏—è —Å–∂–∞—Ç–∏—è */
  }
}

.NameZagolov {
  display: flex; /* –ß—Ç–æ–±—ã —Ä–∞–±–æ—Ç–∞–ª–æ align-items */
  justify-content: center; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ —Ü–µ–Ω—Ç—Ä—É */
  align-items: center;
  margin: 10px 0; /* –û–¥–∏–Ω–∞–∫–æ–≤—ã–µ –æ—Ç—Å—Ç—É–ø—ã —Å–≤–µ—Ä—Ö—É –∏ —Å–Ω–∏–∑—É */
  padding: 5px 10px; /* –î–æ–±–∞–≤–ª—è–µ–º –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã */
  text-align: center; /* –ï—Å–ª–∏ —ç—Ç–æ —Ç–µ–∫—Å—Ç–æ–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç */
}

.BlockLable {
  display: flex;
  align-items: center;
  margin: 10px 0; /* –ß—Ç–æ–±—ã –Ω–µ –±—ã–ª–æ –Ω–∞–ª–æ–∂–µ–Ω–∏–π */
  padding: 5px; /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–π –æ—Ç—Å—Ç—É–ø */
}

.custom-select {
  width: 100%;
  max-width: max;
  padding: 8px;
  font-size: 16px;
  border: 2px solid #3498db;
  border-radius: 6px;
  background-color: white;
  cursor: pointer;
  outline: none;
  transition: 0.3s;
  margin-bottom: 10px; /* –î–æ–±–∞–≤–ª—è–µ–º –æ—Ç—Å—Ç—É–ø –º–µ–∂–¥—É —Å–µ–ª–µ–∫—Ç–∞–º–∏ */
}

.custom-select:last-child {
  margin-bottom: 0; /* –ß—Ç–æ–±—ã —É –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ —Å–µ–ª–µ–∫—Ç–∞ –Ω–µ –±—ã–ª–æ –ª–∏—à–Ω–µ–≥–æ –æ—Ç—Å—Ç—É–ø–∞ */
}

.custom-select:hover {
  border-color: #2980b9;
}

.custom-select:focus {
  border-color: #2ecc71;
  box-shadow: 0 0 8px rgba(46, 204, 113, 0.5);
}

.damage-block {
  margin-top: 25px;
  background: #f9f9f9;
  padding: 10px;
  border-radius: 8px;
}

/* –°—Ç–∏–ª—å –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
.select-container select:hover {
  border-color: #2980b9;
}

/* –°—Ç–∏–ª—å –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ */
.select-container select:focus {
  border-color: #2ecc71;
  box-shadow: 0 0 10px rgba(46, 204, 113, 0.5);
}

/* –°–∫—Ä—ã–≤–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—É—é —Å—Ç—Ä–µ–ª–∫—É */
.select-container select::-ms-expand {
  display: none;
}

/* –ö–∞—Å—Ç–æ–º–Ω–∞—è —Å—Ç—Ä–µ–ª–∫–∞ */
.select-container::after {
  content: "‚ñº";
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  color: #3498db;
  pointer-events: none;
}

.button-container {
  margin-top: 20px; /* –û—Ç—Å—Ç—É–ø —Å–Ω–∏–∑—É –æ—Ç –±–ª–æ–∫–∞ —Å select */
}

.button {
  border: none;
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  color: white;
  background: none;
  cursor: pointer;
  padding: 12px 24px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 10px 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;
  transition: all 0.3s;
  border-radius: 6px;
  gap: 8px;
}

/* –û—Å–Ω–æ–≤–Ω–∞—è –∫–Ω–æ–ø–∫–∞ */
.btn-1 {
  background: #3498db;
  color: #fff;
  position: relative;
  border-radius: 6px;
}

.btn-1:hover {
  background: #2980b9;
}

.btn-1:active {
